import comment from './comment'
import store from './store'

export let data = comment.map((item) => {
  let val = item.value

  if (val < 2) {
    val = val ? val * 10 : 3
  } else if (val > 2 && val < 5) {
    val = val * 1
  } else if (val > 5 && val < 20) {
    val = val / 2
  } else if (val > 20 && val < 50) {
    val = val / 5
  } else {
    val = val / 100
  }

  return {
    name: item.name,
    value: val
  }
})

export const geoCoordMap = store

export const convertData = (data) => {
  let res = []
  for (let i = 0; i < data.length; i++) {
    let geoCoord = geoCoordMap[data[i].name]
    if (geoCoord) {
      res.push({
        // name: data[i].name,
        value: geoCoord.concat(data[i].value)
      })
    }
  }
  return res
}

