  methods: {
    randomeIndex () {
      return Math.floor((Math.random() * data.length))
    },
    rangeRandomSelectPointNum () {
      return Math.floor(Math.random() * (this.rangeSelect[1] - this.rangeSelect[0] + 1) + this.rangeSelect[0])
    },
    rangeRandomSelectPoint () {
      const rangeRandomSelectPointNum = this.rangeRandomSelectPointNum()
      this.select = []
      for (let i = 0; i < rangeRandomSelectPointNum; i++) {
        let randomeIndex = this.randomeIndex()
        const point = data[randomeIndex]

        this.select.push({
          name: point.name,
          value: point.value
        })
      }

      let show = this.showSelect()
      show.then(() => {
        console.log('show end')
        this.hideSelect().then(() => {
          console.log('hide end')
          this.rangeRandomSelectPoint()
        })
      })
    },
    updateRipple (options) {
      const series = Object.assign({
        name: 'Top 5',
        data: convertData(this.select)
      }, options)

      this.option.series = [series]
    },

    async showSelect () {
      await new Promise((resolve) => {
        console.log('show')
        let num = 0
        let tm = setInterval(() => {
          num += 0.1
          if (num <= this.maxScale) {
            this.updateRipple({
              name: 'Top 5',
              rippleEffect: {
                scale: num
              },
              symbolSize: val => {
                if (val[2] < 10) {
                  return val[2] * 5
                } else if (val[2] < 25) {
                  return val[2] * 3
                }

                return val[2]
              }
            })
          } else {
            clearInterval(tm)
            setTimeout(() => {
              resolve(true)
            }, this.keep)
          }
        })
      })
    },

    async hideSelect () {
      await new Promise((resolve) => {
        console.log('hide')

        let num = this.maxScale
        let tm = setInterval(() => {
          num -= 0.2
          if (num >= 1) {
            this.updateRipple({
              name: 'Top 5',
              rippleEffect: {
                scale: num
              }
            })
          } else {
            clearInterval(tm)
            this.option = {
              tooltip: {
                trigger: 'item'
              },
              legend: {
                orient: 'vertical',
                y: 'bottom',
                x: 'right',
                data: ['pm2.5'],
